  created() {
    this.hostKey = this.$route.query.hostKey;
    this.orderList.orderDataList = this.orderList.orderDataList.filter((order) => {
      return (order.orderType.toUpperCase() === 'ORDER');
    });

    axios.all([
      this.getWpassList()
    ]).then(axios.spread((resWpassList) => {
      const list = resWpassList.data.list,
      hostInfo = list.find((host) => {
        return host.hostKey === this.hostKey;
      });

      this.hostInfo = hostInfo;
    }));
  },
  methods : {
    getWpassList() {
      return $axios({
          // url : 'http://localhost:3040/api/configure/v1',
          url : 'https://api.pre.wonder-shopping.com/v1/store/mall',
          method : 'get'
      });
    },
    getLastUpdateTime() {
      if (!this.orderList.orderDataList.length) {
        this.lastUpdateTime = '';
      }

      const dataLastUpdateTime = this.orderList.orderDataList[0].lastUpdateTime;
      return (dataLastUpdateTime) ? moment(new Date(dataLastUpdateTime)).fromNow() : '';
    }
  }
